import React from "react";
import { BiCalendarPlus } from "react-icons/bi"
import { useState,useContext } from "react"



export const AddArticle = ({onSendArticle}) =>{

  const clearData = {
    brand: '',
    name: '',
    quantity: '',
    addedDate: '',
  }

function formDataPublish(){
  const ArticleInfo = {
    brand: formData.brand,
    name: formData.name,
    quantity: formData.quantity,
    addedDate: formData.addedDate,

  }
  onSendArticle(ArticleInfo)
  setFormData(clearData)
  setToggleForm(!toggleForm)
}

  let [toggleForm, setToggleForm] = useState(false)
  let [formData,setFormData] = useState(clearData)
  return(
    <div >
      <button className="bg-blue-400 text-white px-2 py-3 w-full text-left rounded-md ease-in-out transform hover:scale-105 transition duration-500" 
      onClick={()=>{
        setToggleForm(!toggleForm)
        }}>
        <div><BiCalendarPlus className="inline-block align-text-top" />  Add Articles</div>
      </button>
      {
        toggleForm && 
        <div className="border-r-2 border-b-2 border-l-2 border-light-blue-500 rounded-b-md pl-4 pr-4 pb-4">
        <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start  sm:pt-5">
          <label htmlFor="brand" className="block text-sm font-medium text-gray-700 sm:mt-px sm:pt-2">
            Brand
          </label>
          <div className="mt-1 sm:mt-0 sm:col-span-2">
            <input onChange={(event)=>{setFormData({...formData,brand: event.target.value})}} value={formData.brand} type="text" name="brand" id="brand" 
              className="max-w-lg block w-full shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:max-w-xs sm:text-sm border-gray-300 rounded-md" />
          </div>
        </div>

        <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start  sm:pt-5">
          <label htmlFor="name" className="block text-sm font-medium text-gray-700 sm:mt-px sm:pt-2">
            Name
          </label>
          <div className="mt-1 sm:mt-0 sm:col-span-2">
            <input onChange={(event)=>{setFormData({...formData,name: event.target.value})}} type="text" name="name" id="name" value={formData.name}
              className="max-w-lg block w-full shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:max-w-xs sm:text-sm border-gray-300 rounded-md" />
          </div>
        </div>

        <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start  sm:pt-5">
          <label htmlFor="quantity" className="block text-sm font-medium text-gray-700 sm:mt-px sm:pt-2">
            Quantity
          </label>
          <div className="mt-1 sm:mt-0 sm:col-span-2">
            <input onChange={(event)=>{setFormData({...formData,quantity: event.target.value})}} type="text" name="quantity" id="quantity" value={formData.quantity}
              className="max-w-lg block w-full shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:max-w-xs sm:text-sm border-gray-300 rounded-md" />
          </div>
        </div>

      




        <div className="pt-5">
          <div className="flex justify-end">
            <button type="submit" onClick={formDataPublish} className="ml-3 inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-blue-400 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-400">
              Submit
            </button>
          </div>
        </div>
      </div>
      }
    </div>
  )
} 
