 import axios from 'axios'

const api = axios.create({
    baseURL: 'http://localhost:3000/api',
})

export const insertAppointment = payload => api.post(`/appointment`, payload)
export const getAllAppointments = () => api.get(`/appointments`)
export const countAllAppointments = () => api.get(`/appointments/count`)
export const updateAppointmentById = (id, payload) => api.put(`/appointment/${id}`, payload)
export const deleteAppointmentById = id => api.delete(`/appointment/${id}`)
export const getAppointmentById = id => api.get(`/appointment/${id}`)
export const register = payload => api.post(`/register`,payload)
export const login = payload => api.post(`/login`,payload)
export const updateUser = (pseudo,payload,token) => api.put(`/user/${pseudo}`,payload,token)
export const updatePassword = (pseudo,payload,token) => api.put(`/user/modify/${pseudo}`,payload,token)


const apis = {
    insertAppointment,
    getAllAppointments,
    updateAppointmentById,
    deleteAppointmentById,
    getAppointmentById,
    countAllAppointments,
    login,
    register,
    updateUser,
    updatePassword,
    
}

export default apis