import {AddArticle} from "../components/AddArticles"

import { useState,useEffect,useCallback } from "react"
import Navbar from "../components/Navbar"

import articlesApi from "../api/ArticlesApi"

export const Articles = ()=>{


  const [error, setError] = useState(null)
  const [dataIsLoaded, setDataIsLoaded] = useState(false)
  let [articles, setArticles] = useState([])
   


  

  // Remarque : le tableau vide de dpendances [] indique
  // que useEffect ne sexcutera quune fois, un peu comme
  // componentDidMount()

  const fetchData = useCallback(()=>{
      articlesApi.getAllArticles()
          .then((result) => {
              setDataIsLoaded(true);
              setArticles(result.data.data);
          },(error) => {
              setDataIsLoaded(true);
              setError(error);
            });
      // eslint-disable-next-line react-hooks/exhaustive-deps
      },[])
  
  useEffect(()=>{fetchData()
  },[fetchData,dataIsLoaded])

  if (error) {
      return <div>Erreur : {error.message}</div>;
  } else if (!dataIsLoaded) {
      return <div>Chargement...</div>;
  } else {
    return (

      <div>
      <Navbar />
        <div className="container mx-auto px-4 sm:px-8 max-w-3xl">
          <div className="py-8">
            <AddArticle onSendArticle = {
              article=>{
                articlesApi.createArticle(article)
                setDataIsLoaded(false)}}/>
           <div className="-mx-4 sm:-mx-8 px-4 sm:px-8 py-4 overflow-x-auto">

            
            <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">

            

              <table className="min-w-full leading-normal">
                <thead>
                  <tr>
                    <th scope="col" className="px-5 py-3 bg-white  border-b border-gray-200 text-gray-800  text-left text-sm uppercase font-normal">
                        Article
                    </th>
                    <th scope="col" className="px-5 py-3 bg-white  border-b border-gray-200 text-gray-800  text-left text-sm uppercase font-normal">
                      Brand
                    </th>
                    <th scope="col" className="px-5 py-3 bg-white  border-b border-gray-200 text-gray-800  text-left text-sm uppercase font-normal">
                      Quantity
                    </th>
                    <th scope="col" className="px-5 py-3 bg-white  border-b border-gray-200 text-gray-800  text-left text-sm uppercase font-normal">
                        Added at
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {articles.map(article =>(
                  <tr>
                    <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                      <div className="flex items-center">
                        <div className="ml-3">
                          <p className="text-gray-900 whitespace-no-wrap">
                            {article.name}
                          </p>
                        </div>
                      </div>
                    </td>
                    <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                      <p className="text-gray-900 whitespace-no-wrap">
                          {article.brand}
                      </p></td>
                    <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                      <p className="text-gray-900 whitespace-no-wrap">
                          {article.quantity}
                                </p>
                            </td>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <span className="relative inline-block px-3 py-1 font-semibold text-green-900 leading-tight">
                                    <span aria-hidden="true" className="absolute inset-0 bg-green-200 opacity-50 rounded-full">
                                    </span>
                                    <span className="relative">
                                  {article.addedDate}
                                    </span>
                                </span>
                            </td>
                        </tr>
                        ))}
                        

                    </tbody>
                </table>
            </div>
        </div>
    </div>
        </div>
        </div>
    )
}}