import React, { useState } from "react"
import { Link,useNavigate} from "react-router-dom"
import PasswordStrengthBar from 'react-password-strength-bar'
import { useToken } from "../auth/useToken"
import apis from "../api"

const Register = ()=>{
    const [token,setToken]=useToken()
    const [pseudo,setPseudo]=useState('')
    const [firstName,setFirstName]=useState('')
    const [lastName,setLastName]=useState('')
    const [emailValue,setEmailValue]=useState('')
    const [passwordValue,setPasswordValue]=useState('')
    const [confirmPasswordValue,setConfirmPasswordValue]=useState('')
    const navigate = useNavigate()

    const onSignUpClicked = async()=>{
        await apis.register({
            pseudo: pseudo,
            email: emailValue,
            password: passwordValue,
            firstName : firstName,
            lastName: lastName,
            isVerified: false,
        }).then((response)=>{
            const {token} = response.data
        setToken(token)
        navigate('/',{replace: true})
        })
    }


    return(
        <section class="h-screen bg-gray-100 bg-opacity-50 flex place-content-center items-center w-screen ">
            <div class="flex flex-col max-w-md px-4 py-8 bg-white rounded-lg shadow-2xl dark:bg-gray-800 sm:px-6 md:px-8 lg:px-10">
                <div class="self-center justify-center w-1/4 h-full pr-4 mt-2">
                    <Link to='/'><a href="#_" class="inline-block py-4 md:py-0">
                        <span class="p-1 text-xl font-black leading-none text-gray-900"><span>TrackPet</span><span class="text-indigo-600">.</span></span>
                    </a></Link>
                </div>
                <div class="self-center mb-2 text-xl font-light text-gray-800 sm:text-2xl dark:text-white">
                    Create a new account
                </div>
                <span class="justify-center text-sm text-center text-gray-500 flex-items-center dark:text-gray-400">
                    Already have an account ?
                    <Link to='/login' class="text-sm text-blue-500 underline hover:text-blue-700">
                        Sign in
                    </Link>
                </span>
                <div class="p-6 mt-8">
                        <div class="flex flex-col mb-2">
                            <div class=" relative ">
                                <input type="text" value={pseudo} onChange={e=>setPseudo(e.target.value)} id="create-account-pseudo" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" name="pseudo" placeholder="Pseudo"/>
                            </div>
                        </div>
                        <div class="flex gap-4 mb-2">
                            <div class=" relative ">
                                <input type="text" value={firstName} onChange={e=>setFirstName(e.target.value)} id="create-account-first-name" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" name="First name" placeholder="First name"/>
                            </div>
                            <div class=" relative ">
                                <input type="text" value={lastName} onChange={e=>setLastName(e.target.value)} id="create-account-last-name" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" name="Last name" placeholder="Last name"/>
                            </div>
                        </div>
                        <div class="flex flex-col mb-2">
                            <div class=" relative ">
                                <input type="text" value={emailValue} onChange={e=>setEmailValue(e.target.value)} id="create-account-email" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Email"/>                                        
                            </div>
                        </div>
                        <div class="flex flex-col mb-2">
                            <input type="password" id="with-indications" value={passwordValue} onChange={e=>setPasswordValue(e.target.value)} class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" name="password" placeholder="Password"/>
                            <PasswordStrengthBar password={passwordValue} className="mt-2" />
                        </div>
                        <div class="flex flex-col mb-2">
                            <input type="password" value={confirmPasswordValue} onChange={e=>setConfirmPasswordValue(e.target.value)} class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" name="confirmPassword" placeholder="Password"/>
                        </div>
                        <div class="flex w-full my-4">
                            <button onClick={onSignUpClicked} disabled={!emailValue || !firstName || !lastName || !pseudo || !confirmPasswordValue || !passwordValue || confirmPasswordValue!==passwordValue } type="submit" class="py-2 px-4 disabled:bg-gray-300 bg-purple-600 hover:bg-purple-700 focus:ring-purple-500 focus:ring-offset-purple-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                                Sign up
                            </button>
                        </div>  
                </div>
            </div>
        </section>
    )
}
export default Register