import React, {useState} from "react";
import { useNavigate } from "react-router";
import { useUser } from "../auth/useUser";
import { useToken } from "../auth/useToken";
import axios from "axios";
import apis from "../api";


export const UserInfo = ()=>{
    //Helpers hooks
    const user = useUser()
    const navigate = useNavigate()
    const [token,setToken] = useToken()



    //Form Values hooks
    const [pseudo,setPseudo]=useState(user.pseudo || '')
    const [firstName,setFirstName]=useState(user.firstName || '')
    const [lastName,setLastName]=useState(user.lastName || '')
    const [emailValue,setEmailValue]=useState(user.email || '')
    const [oldPasswordValue,setOldPasswordValue]=useState(user.password || '')
    const [newPasswordValue,setNewPasswordValue]=useState('')
    const [confirmPasswordValue,setConfirmPasswordValue]=useState('')


    //Messages hooks
    const [showSuccessMessage,setShowSuccessMessage]=useState(false)
    const [showErrorMessage,setShowErrorMessage]=useState(false)

    const onChangePassword=async()=>{

        await apis.updateUser(pseudo,{oldPassword: oldPasswordValue,password: newPasswordValue},{headers: {Authorization: `Bearer ${token}`}}
        ).then((response)=>{
            const {token: newToken}= response.data
            setToken(newToken)
            setShowSuccessMessage(true)
        }).catch((error)=>{
            setShowErrorMessage(true)
        })
    }

    const onSave=async()=>{
        await apis.updateUser(pseudo,{lastName: lastName,firstName: firstName},{headers: {Authorization: `Bearer ${token}`}}
        ).then((response)=>{
            const {token: newToken}= response.data
            setToken(newToken)
            setShowSuccessMessage(true)
        }).catch((error)=>{
            setShowErrorMessage(true)
        })
    }

    return (
        
        <section class="h-screen bg-gray-100 bg-opacity-50 flex place-content-center items-center w-screen ">
            <div className="container max-w-2xl mx-auto shadow-2xl md:w-3/4">
                <div class="p-4 bg-gray-100 border-t-2 border-indigo-400 rounded-lg bg-opacity-5">
                    <div class="max-w-sm mx-auto md:w-full md:mx-0">
                        <div class="inline-flex items-center space-x-4">
                            <h1 class="text-gray-600">
                                Edit Your Account
                            </h1>
                        </div>
                    </div>
                </div>
                <div class="space-y-6 bg-white">
                    <div class="items-center w-full p-4 space-y-4 text-gray-500 md:inline-flex md:space-y-0">
                        <h2 class="max-w-sm mx-auto md:w-1/3">
                            Account
                        </h2>
                        <div class="max-w-sm mx-auto md:w-2/3">
                            <div class=" relative ">
                                <input type="text" disabled='yes' autocomplete="off" value={emailValue} onChange={e=>setEmailValue(e.target.value)} id="user-info-email" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Email"/>
                                </div>
                            </div>
                        </div>
                        <hr/>
                        <div class="items-center w-full p-4 space-y-4 text-gray-500 md:inline-flex md:space-y-0">
                            <h2 class="max-w-sm mx-auto md:w-1/3">
                                Personal info
                            </h2>
                            <div class="max-w-sm mx-auto space-y-5 md:w-2/3">
                                <div>
                                    <div class=" relative ">
                                        <input type="text" autocomplete="off" value={firstName} onChange={e=>setFirstName(e.target.value)}  id="user-info-firstName" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="First Name"/>
                                    </div>
                                    <div class=" relative ">
                                        <input type="text" autocomplete="off" value={lastName} onChange={e=>setLastName(e.target.value)} id="user-info-lastName" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Last Name"/>
                                    </div>


                                </div>
                                    
                                <div>
                                    <div class=" relative ">
                                        <input type="text" autocomplete="off" disabled='yes' value={pseudo} id="user-info-pseudo" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Pseudo"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <hr/>
                        <div class="items-center w-full p-8 space-y-4 text-gray-500 md:inline-flex md:space-y-0">
                            <h2 class="max-w-sm mx-auto md:w-4/12">
                                    Change password
                            </h2>
                            <div class="w-full max-w-sm pl-2 mx-auto space-y-5 md:w-5/12 md:pl-9 md:inline-flex">
                                <div class=" relative ">
                                    <input type="password" autoComplete="off" onChange={e=>setOldPasswordValue(e.target.value)} id="user-info-oldPassword" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Old Password"/>
                                </div>   
                            </div>
                            <div class="w-full max-w-sm pl-2 mx-auto space-y-5 md:w-5/12 md:pl-9 md:inline-flex">
                                <div class=" relative ">
                                    <input type="password" autocomplete="off" onChange={e=>setNewPasswordValue(e.target.value)} id="user-info-newPassword" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="New Password"/>
                                </div>
                            </div>
                            <div class="w-full max-w-sm pl-2 mx-auto space-y-5 md:w-5/12 md:pl-9 md:inline-flex">
                                <div class=" relative ">
                                    <input type="password" autocomplete="off" onChange={e=>setConfirmPasswordValue(e.target.value)} id="user-info-re-newPassword" class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent" placeholder="Confirm Password"/>
                                </div>
                            </div>
                                        <div class="text-center md:w-3/12 md:pl-6">
                                            <button onClick={onChangePassword} disabled={!oldPasswordValue || !newPasswordValue || !confirmPasswordValue || confirmPasswordValue!==newPasswordValue} type="button" class=" disabled:bg-gray-300 py-2 px-4  bg-pink-600 hover:bg-pink-700 focus:ring-pink-500 focus:ring-offset-pink-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                                                Change
                                            </button>
                                        </div>
                                    </div>
                                    <hr/>
                                    <div class="w-full px-4 pb-4 ml-auto text-gray-500 md:w-1/3">
                                        <button type="button" onClick={onSave} class="py-2 px-4  bg-blue-600 hover:bg-blue-700 focus:ring-blue-500 focus:ring-offset-blue-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                                            Save
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </section>
        )
}